@font-face {
	font-family: "Inter-Variable";
	font-display: swap;
	src: url("assets/fonts/Inter-VariableFont.woff") format("woff"),
		url("assets/fonts/Inter-VariableFont.woff2") format("woff2");
	font-weight: 400;
	font-style: normal;
}
@font-face {
	font-family: "WazzardM";
	font-display: swap;
	src: url("assets/fonts/MazzardM-Regular.woff") format("woff"),
		url("assets/fonts/MazzardM-Regular.woff2") format("woff2");
	font-weight: 400;
	font-style: normal;
}
$fontFamily: "Inter-Variable";
$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1410;
$md1: $maxWidthContainer + 30;
$md2: 991.98;
$md3: 767.98;
$md4: 575.98;

body {
	background: #f6f6f6;
}
#root {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	font-family: $fontFamily, sans-serif;
	margin: auto;
	width: 100%;
	height: 100%;
	min-height: 100vh;
	scroll-behavior: smooth;
}
._container {
	width: 100%;
	max-width: $md1 + px;
	margin: 0 auto;
	padding: 0 20px;
}

.active-link {
	color: red;
}

.page {
	flex: 1 1 auto;
}

/* Safari */

.scrollToTop {
	position: fixed;
	bottom: 50px;
	right: 50px;
	height: 46px;
	width: 45px;
	border-radius: 50%;
	padding: 4px 16px;

	border: 2px solid lightgray;
	opacity: 0;
	transition: 0.5s;
	transform: translateY(100px) rotate(-90deg) scale(1);
	z-index: 3;
}
.scrollToTop.showBtn {
	background-color: #1a1a25;
	// transform: rotateX("90deg");
	opacity: 1;
	transform: translateY(0px) rotate(-90deg) scale(1);
}

.scrollToTop:hover.showBtn {
	transform: translateY(0px) rotate(-90deg) scale(1.3);
}

@media (max-width: ($md3+px)) {
	._container {
		padding: 0 10px;
	}
	.scrollToTop {
		bottom: 50px;
		right: 20px;
	}
}
