.basket-page {
	padding: 100px 15px 50px 15px;
	margin: auto;
	max-width: 1440px;
	&__grid {
		margin: 0 0 120px 0;
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(270px, 300px));
		gap: 30px;
		// margin: auto;
		// justify-items: center;
		& > div {
			width: 100%;
		}
	}
}
